
import { defineComponent } from "vue";
export default defineComponent({
  props: {
    audioList: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  methods: {
    to_detail(item: { id: number }, index: number) {
      this.$router.push({
        name: "articleDetail",
        params: { id: item.id, index },
      });
    },
  },
});
